import React, { Component } from 'react'
import medal from './medal-solid.svg'
import './Results.css'
import { FaMedal} from "react-icons/fa";
export default class Resultcard extends Component {
  constructor(props) {
    super();
    this.state = {};
  }
    render() {
        return (
            <div>
                 <div className="card0">
                <div className="imgBox">
                  <img src={this.props.rimage}  style={{height:'360px'}} />
                </div>
                <div className="details0">
               {/*} <div style={{textAlign:'center'}}>
            <strong > {this.props.rtitle}</strong>
          </div>   <br/>   
          <br/>
          <div>
            Winner :{this.props.rwinner}
            <br />   <br/>
            First Runner Up : {this.props.r1}
            <br />   <br/>
            Second Runner Up : {this.props.r2}
        </div>*/}
                   
          <section class="random-bg">
    <div class="winner-wrap">
      <div class="border"></div>
      <div class="medal-box"><FaMedal/> </div>
      <h1>{this.props.rtitle}</h1>
      
      <div class="winner-ribbon">WINNER</div>
      <div class="right-ribbon"></div>
      <div class="left-ribbon"></div>
     
      <div class="winner-ribbon1">FIRST RUNNER UP</div>
      <div class="right-ribbon1"></div>
      <div class="left-ribbon1"></div>

       <div class="winner-ribbon2">SECOND RUNNER UP</div>
      <div class="right-ribbon2"></div>
      <div class="left-ribbon2"></div>
      <div class="wp"><h2 style={{textAlign:'center',width:'90%',color:'#0C6A51'}}>{this.props.rwinner}<br/><span style={{fontSize:'0.9rem'}}>{this.props.rwc}</span></h2></div>
      <div class="wp1"><h2 style={{textAlign:'center',width:'90%',color:'#0C6A51'}}>{this.props.r1}<br/><span style={{fontSize:'0.8rem'}}>{this.props.r1c}</span></h2></div>
      <div class="wp2"><h2 style={{textAlign:'center',width:'90%',color:'#0C6A51'}}>{this.props.r2}<br/><span style={{fontSize:'0.8rem'}}>{this.props.r2c}</span></h2></div>


    </div>
  

                </section>
                </div>
              </div>
            </div>
        )
    }
}
